<!-- 测试查询历史记录 -->
<template>
  <div class="bg">

    <button @click="index += 1">自增加{{ index }}</button>
    <button @click="pop">返回</button>
    <div><button @click="addPhoneState('15811024484')">PhoneState: 15811024484</button></div>
    <div><button @click="addPhoneState('13700001111')">PhoneState: 13700001111</button></div>

    <!-- 出行探针 -->
    <div><button @click="addTravel(['李刚', '360822198609284091'])">出行探针-名字: 李刚 360822198609284091</button></div>
    <div><button @click="addTravel('元芳-36082219750711058X')">出行探针-名字: 元芳 36082219750711058X</button></div>

    <div v-for="(tag, k) in getTags">
      <span> {{ tag }} </span>
    </div>

  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    name: 'TestTag',
    components: {

    },
    props: {

    },
    data () {
      return {
        index: 0,
      }
    },
    methods: {
      pop() {
        this.$router.go(-1)
      },

     addPhoneState ( searchKey) {
        let searchType = gconst.SearchType.PHONE_STATE
       this.$store.dispatch('tags/addTag', {
         searchKey,
         searchType
       })

       console.log("TestTag activated addPhoneState:" + this.$store.state.tags.tags)
     },

      addTravel(searchKey) {
        let searchType = gconst.SearchType.PROBE

        this.$store.dispatch('tags/addTag', {
          searchKey,
          searchType
        })
        console.log("TestTag activated addTravel:" + this.$store.state.tags.tags)
      },

      getName (searchType) {
       let title = gddmgr.getScreenTitle(searchType)
        return title
      },
    },
    computed: {
      ...mapGetters('tags', ['getTags'])
    },
    created() {
      console.log("-----------------111 created")

    },

    // activated触发需要在router-view外套层keep-alive
    activated() {
      console.log("TestTag activated:" + this.$store.state.tags.tags)
    }
  }
</script>

<style scoped>
  .bg {
    width: 100%;
    height: 100%;
    background: #F0F0F0;
    overflow: auto; /* 垂直滚动条 */
  }

  .root {
    padding: 20px 50px 0px;
  }
</style>



